import fetch from '@/utils/fetch1';
const qs = require('qs');

export function dataLists(params) {
  return fetch({
    // baseURL: 'https://easy-mock.com/mock/59a44c219d8ef6191d07f0af/inwill',
    url: '/api/admin/getBookList',
    method: 'post',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    data: qs.stringify(params)
  });
}

